<template>
  <div style="height: 100%;">
    <Toolbar>
      <template v-slot:title>
        {{chat ? chat.title : 'Chat name'}}
      </template>
      <template v-slot:append v-if="chat">
        <Settings/>
      </template>
    </Toolbar>
    <v-flex 
      v-if="chat && wsStatus === 'success'"
      style="position: relative;"
      xs12
    >
      <div class="chat-container" ref="scrollableDiv">
        <div
          class="message"
          v-for="(m, index) in chat.messages"
          v-bind:key="m.id"
          :class="{own: m.user_id == credentials.id}"
        >
          <!-- todo: add usernames to messages -->
          
          <!-- <div class="username" v-if="index == 0">{{ m.user.username }}</div>
          <div class="username" v-else-if="chat.messages[index].user_id != chat.messages[index-1].user_id">{{ m.user.username }}</div> -->
          <div style="margin-top: 5px"></div>
          <div class="content">
            <div v-html="m.body"></div>
          </div>
        </div>
      </div>
      <v-text-field
        solo
        class="px-4 pt-2"
        v-on:keyup.enter="sendMessage"
        v-model="message"
        :counter="255"
        :placeholder="'Your message'"
        append-outer-icon="mdi-send"
        @click:append-outer="sendMessage"
      ></v-text-field>
    </v-flex>
    <Loader v-else-if="wsStatus === 'loading'"/>
    <v-layout
        v-else
        align-center
        justify-center
        row fill-height
    >
        <div class="text-xs-center">
          <v-chip
            style="opacity: 0.6;"
            small
            dark
          >Please select a chat to start a conversation</v-chip>
        </div>
    </v-layout>
  </div>
</template>

<script>
import Toolbar from '@/components/Toolbar'
import Settings from '@/components/ChatSettings'

export default {
  name: 'Chat',
  data: () => ({
    message: ''
  }),
  components: {
    Toolbar,
    Settings
  },
  methods: {
    sendMessage () {
      if (this.message !== '') {
        const message = {
          body: this.message,
          chat_identifier: this.chat.identifier,
          user_id: this.senderId
        }
        this.$store.dispatch('wsSendMessage', message).then(() => {
          this.message = ''
        }).catch(err => {
          console.log("web socket error")
        })
      }
    },
    scroll () {
      this.$refs.scrollableDiv.scrollTop = this.$refs.scrollableDiv.scrollHeight
    }
  },
  computed: {
    chat () {
      return this.$store.state.chat.chat
    },
    credentials () {
      return this.$store.state.user.userCredentials
    },
    wsStatus () {
      return this.$store.state.chat.wsStatus
    }
  }
}
</script>

<style>
.chat-container {
  box-sizing: border-box;
  height: calc(100vh - 7.8rem);
  overflow-y: auto;
  padding: 10px;
  background-color: inherit;
}
.message {
  margin-bottom: 3px;
}
.message.own {
  text-align: right;
}
.message.own .content {
  background-color: lightskyblue;
}
.chat-container .username {
  font-size: 18px;
  font-weight: bold;
}
.chat-container .content {
  padding: 8px;
  background-color: #FAFAFA;
  border-radius: 10px;
  display: inline-block;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 2px 1px -1px rgba(0, 0, 0, 0.12);
  max-width: 50%;
  word-wrap: break-word;
}
</style>
